<template>
  <div id="find-a-provider" ref="app">
    <div v-if="state === 'listing'">
      <div v-if="listingState === 'loading'">
        <p class="find-a-provider__message">Loading...</p>
      </div>
      <template v-if="listingState === 'display'">
        <form-filter :provider-list="filterProviderList" @updateFilter="filterChange" />
        <results :list="paginatedView" @selected="selectedResult"/>
        <pager v-if="totalSteps > 1" :initial="pager.currentStep" :perstep="pager.itemsPerStep" :total="totalSteps" @change="pagerChange"/>
      </template>
      <error v-if="listingState === 'error'" @retry="load" />
    </div>
    <div v-if="state === 'full'">
      <full-view :selected="selected.register" @back="backClick" />
    </div>
  </div>
</template>

<script>
// import localDataset from './assets/dataset.json'
import axios from 'axios'
import FormFilter from './components/FormFilter'
import Results from './components/Results'
import Pager from './components/Pager'
import FullView from './components/FullView'
import Error from './components/Error'

export default {
  name: 'App',
  components: {
    FormFilter,
    Results,
    Pager,
    FullView,
    Error
  },
  data () {
    return {
      dataset: null,
      filters: {
        keywords: null,
        provider: null,
        sort: 'asc'
      },
      filterProviderList: null,
      pager: {
        currentStep: 1,
        itemsPerStep: 6
      },
      state: 'listing',
      listingState: 'loading',
      selected: null
    }
  },
  computed: {
    processedList () {
      let curatedDataset = []
      if (this.dataset) {
        for (let i = 0; i < this.dataset.registerDetails.length; i++) {
          const register = this.dataset.registerDetails[i]
          const participantType = register.participantType
          const displayName = register.legalEntityName || ''
          curatedDataset.push({
            id: register.legalEntityRef,
            logo: register.logoUrl,
            title: displayName,
            displayName: displayName,
            participantType: participantType,
            participantLabelColor: (participantType === 'Data Recipient') ? 'orange' : 'purple',
            status: register.status,
            reference: register.legalEntityRef,
            keywordSearch: displayName.toUpperCase(),
            register: register
          })
        }
        curatedDataset = this.filterList(curatedDataset)
        this.sortList(curatedDataset)
      }
      return curatedDataset
    },
    paginatedView () {
      const from = (this.pager.currentStep - 1) * this.pager.itemsPerStep
      const to = from + this.pager.itemsPerStep
      return this.processedList.slice(from, to)
    },
    totalSteps () {
      return Math.ceil(this.processedList.length / this.pager.itemsPerStep)
    }
  },
  methods: {
    async load () {
      try {
        this.listingState = 'loading'
        const result = await axios('https://api.test.cdr.gov.au/cdr-register/v1/banking/register')
        if (result.status === 200 && result.data) {
          this.dataset = result.data
          const providers = new Set()
          this.dataset.registerDetails.forEach(item => {
            providers.add(item.participantType)
          })
          this.filterProviderList = Array.from(providers).map(item => ({ label: item, value: item }))
          this.listingState = 'display'
        } else {
          this.listingState = 'error'
        }
      } catch (e) {
        console.log(e)
      }
    },
    filterChange (filters) {
      this.filters.keywords = filters.keywords
      this.filters.provider = filters.provider
      this.filters.sort = filters.sort
      this.pager.currentStep = 1
    },
    pagerChange (page) {
      this.pager.currentStep = page
      this.$refs['app'].scrollIntoView({ behavior: 'smooth' })
    },
    selectedResult (result) {
      this.$router.push({ query: { provider: result.id } })
    },
    backClick () {
      this.$router.push({ query: {} })
    },
    filterList (list) {
      const hasFilters = (this.filters.keywords || this.filters.provider || this.filters.product || this.filters.organisation)
      if (hasFilters) {
        const returnList = []
        const keywords = this.filters.keywords ? this.filters.keywords.toUpperCase() : false
        const provider = this.filters.provider ? this.filters.provider : false
        list.forEach(item => {
          let addToResults = false
          // Keyword
          if (keywords) {
            if (item.keywordSearch.indexOf(keywords) > -1) {
              addToResults = true
            }
          }
          // Type of provider
          if (provider) {
            if (item.participantType === provider) {
              addToResults = true
            }
          }
          if (addToResults) {
            returnList.push(item)
          }
        })
        return returnList
      } else {
        return list
      }
    },
    sortList (list) {
      if (this.filters.sort) {
        if (this.filters.sort === 'asc') {
          list = list.sort((a, b) => a.displayName.localeCompare(b.displayName))
        } else {
          list = list.sort((a, b) => b.displayName.localeCompare(a.displayName))
        }
      }
      return list
    },
    setState (query) {
      if (query.provider) {
        let result = null
        for (let i = 0; i < this.processedList.length; i++) {
          const item = this.processedList[i]
          if (item.id === query.provider) {
            result = item
            break
          }
        }
        if (result) {
          this.state = 'full'
          this.selected = result
        }
      } else {
        this.state = 'listing'
        this.selected = null
      }
      // Reset filters
      this.filters = {
        keywords: null,
        provider: null,
        sort: 'asc'
      }
    }
  },
  created () {
    this.load()
  },
  mounted () {
    this.setState(this.$route.query)
  },
  watch: {
    $route: function (to) {
      this.setState(to.query)
    }
  }
}
</script>

<style lang="scss">
body, button, select, input {
  font-family: freight-sans-pro, sans-serif;
  font-style: normal;
  font-feature-settings:"pnum", "lnum";
}
</style>
