<template>
  <div class="full-view">
    <button @click="back" class="full-view__back">Back</button>
    <img class="full-view__logo" v-if="selected.logoUrl" :src="selected.logoUrl" alt="" />
    <h2 class="full-view__title">{{ selected.legalEntityName }}</h2>
    <div class="full-view__details">
      <div v-for="(field, index) in details" :key="index" class="full-view__detail">
        <h3 class="full-view__detail-title">{{ field.title }}</h3>
        <p class="full-view__detail-detail" v-html="field.detail"></p>
      </div>
    </div>
    <div class="full-view__brand-setion" v-if="selected.brands && selected.brands.length > 0">
      <h3 class="full-view__brand-title">Brands</h3>
      <ul class="full-view__brands">
        <li v-for="(brand, index) of selected.brands" :key="index" class="full-view__brands-item">
          <div class="full-view__brands-wrapper">
            <img class="full-view__brands-image" v-if="brand.logoUrl" :src="brand.logoUrl" alt="" />
            <div class="full-view__detail" v-if="brand.brandName">
              <h4 class="full-view__detail-title">Brand Name</h4>
              <p class="full-view__detail-detail">{{ brand.brandName }}</p>
            </div>
            <div class="full-view__detail" v-if="brand.brandDescription">
              <h4 class="full-view__detail-title">Brand Description</h4>
              <p class="full-view__detail-detail">{{ brand.brandDescription }}</p>
            </div>
            <div class="full-view__detail" v-if="brand.status">
              <h4 class="full-view__detail-title">Status</h4>
              <p class="full-view__detail-detail">{{ brand.status }}</p>
            </div>
            <div v-if="brand.softwareProducts && brand.softwareProducts.length > 0">
              <h4 class="full-view__detail-title">Products</h4>
              <ul v-for="(software, index) in brand.softwareProducts" :key="index">
                <li>
                  <div>{{ software.softwareProductRef }}</div>
                  <div>{{ software.softwareProductName }}</div>
                  <div>{{ software.softwareProductStatus }}</div>
                </li>
              </ul>
            </div>
          </div>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Results',
  props: {
    selected: Object
  },
  computed: {
    address () {
      const address = []
      const street = []
      if (this.selected.serviceAddressStreetAddress1) {
        street.push(this.selected.serviceAddressStreetAddress1)
      }
      if (this.selected.serviceAddressStreetAddress2) {
        street.push(this.selected.serviceAddressStreetAddress2)
      }
      if (street.length > 0) {
        address.push(street.join(' '))
      }
      if (this.selected.serviceAddresState) {
        address.push(this.selected.serviceAddresState)
      }
      if (this.selected.serviceAddressPostcode) {
        address.push(this.selected.serviceAddressPostcode)
      }
      if (this.selected.serviceAddressCountry) {
        address.push(this.selected.serviceAddressCountry)
      }
      return address.join(', ')
    },
    details () {
      const fields = [
        { field: 'lastUpdated', title: 'Last updated', type: 'text' },
        { field: 'legalEntityRef', title: 'Accreditation Number', type: 'text' },
        { field: 'status', title: 'Accreditation Status', type: 'text' },
        { field: 'website', title: 'Website', type: 'link' },
        { field: 'cdrPolicyUrl', title: 'Consumer Data rights', type: 'link' },
        { field: 'address', title: 'Address for service', type: 'text' },
        { field: 'legalEntityName', title: 'Legal entity name', type: 'text' },
        { field: 'legalEntityRef', title: 'Legal entity reference', type: 'text' }
      ]
      const returnFields = []
      fields.forEach(field => {
        const fieldVal = this.selected[field.field]
        if (fieldVal) {
          let html = ''
          switch (field.type) {
            case 'link':
              html = `<a class="full-view__detail-link" href="${fieldVal}">${fieldVal}</a>`
              break
            case 'text':
              html = fieldVal
              break
          }
          returnFields.push({ title: field.title, detail: html })
        }
      })
      return returnFields
    }
  },
  methods: {
    back () {
      this.$emit('back')
    }
  }
}
</script>

