<template>
  <form class="filter" @submit="updateFilters">
    <div class="filter__first-line">
      <div class="filter__field filter__keyword">
        <label class="filter__label filter__keyword-label" for="filter__keyword">Search for a provider</label>
        <input class="filter__input filter__keyword-input" id="filter__keyword" v-model="filter.keywords" type="text" placeholder="Search for a provider" @keyup="updateFilters" />
      </div>
      <div class="filter__field filter__provider">
        <label class="filter__label filter__provider-label" for="filter__provider">Filter by type of provider</label>
        <div class="filter__input-select-wrapper">
          <select class="filter__provider-input" id="filter__provider" v-model="filter.provider" @change="updateFilters">
            <option value="">All providers</option>
            <option v-for="(item, idx) in providerList" :key="idx" :value="item.value">{{ item.label }}</option>
          </select>
        </div>
      </div>
    </div>
    <div class="filter__sort">
       <label class="filter__label filter__sort-label" for="filter__sort">Sort</label>
       <select class="filter__sort-input" id="filter__sort" v-model="filter.sort" @change="updateFilters">
         <option value="asc">Sort (A-Z)</option>
         <option value="desc">Sort (Z-A)</option>
       </select>
    </div>
  </form>
</template>

<script>
export default {
  name: 'FormFilter',
  components: {},
  props: {
    providerList: Array
  },
  data () {
    return {
      filter: {
        keywords: '',
        provider: '',
        sort: 'asc'
      }
    }
  },
  methods: {
    updateFilters () {
      this.$emit('updateFilter', this.filter)
    }
  }
}
</script>

